/**
 * Part of Speech (POS) localization by target language.
 *
 * This module provides localized POS labels based on the target language
 * (the language being learned), NOT the user's interface language.
 *
 * For example, when learning Japanese, POS labels should be shown in Japanese
 * (e.g., "名詞" instead of "noun"), regardless of the user's UI language setting.
 */

/**
 * Supported language codes for POS localization.
 * Maps to BCP 47 language tags used in the application.
 */
type PosLanguageCode = 'en' | 'zh-CN' | 'zh-TW' | 'ja' | 'ko' | 'es' | 'fr' | 'de'

