/**
 * Get localized POS label based on target language.
 *
 * @param pos - POS code from database (e.g., 'noun', 'verb', 'adj')
 * @param targetLang - Target language code (the language being learned)
 * @returns Localized POS label, or original POS if not found
 *
 * @example
 * getPosLabel('noun', 'ja')     // '名詞'
 * getPosLabel('verb', 'zh-CN')  // '动词'
 * getPosLabel('adj', 'en')      // 'adj.'
 * getPosLabel('unknown_pos', 'ja')  // 'unknown_pos' (fallback)
 */
export function getPosLabel(pos: string, targetLang: string): string {
  if (!pos) return ''

  const labels = POS_LABELS[pos]
  if (!labels) {
    // Unknown POS code, return as-is
    return pos
  }

  // Try to get label for target language
  const label = labels[targetLang as PosLanguageCode]
  if (label) {
    return label
  }

  // Fallback to English label
  const englishLabel = labels['en']
  if (englishLabel) {
    return englishLabel
  }

  // Last resort: return original POS code
  return pos
}
