import { VideoCollectionCreateRequest, VideoCollectionResponse } from './client'
import { CollectionInfo, MessageType, MessageSource } from '../types/messages'
import { sendMessageWithResponse } from './messageUtils'

/**
 * CollectionBridge - Bridge layer for collection operations
 *
 * Provides a clean API for Content Script and Popup to interact with
 * CollectionManager in Background Service via message passing.
 *
 * All methods are async and handle the message passing transparently.
 *
 * Note: Most methods use CONTENT_SCRIPT as source, but removeCollection
 * supports both CONTENT_SCRIPT and POPUP sources.
 */
export const CollectionBridge = {
  /**
   * Load collections for a specific video.
   *
   * @param videoViewId - Video view ID
   * @param sourceLang - Source language code
   * @returns Array of CollectionInfo
   */
  async loadCollections(videoViewId: number, sourceLang: string): Promise<CollectionInfo[]> {
    const response = await sendMessageWithResponse<{ collections: CollectionInfo[] }>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.COLLECTION_LOAD,
      { videoViewId, sourceLang },
    )
    return response.collections
  },

  /**
   * Remove a collection.
   * Can be called with or without videoViewId:
   * - With videoViewId: faster lookup in specific cache
   * - Without videoViewId: searches all caches (for Popup use)
   *
   * @param collectionId - Collection UUID to remove
   * @param videoViewId - Optional video view ID for faster lookup
   * @param source - Message source (defaults to CONTENT_SCRIPT, use POPUP for Popup)
   */
  async removeCollection(
    collectionId: string,
    videoViewId?: number,
    source: MessageSource = MessageSource.CONTENT_SCRIPT,
  ): Promise<void> {
    await sendMessageWithResponse<{ success: boolean }>(source, MessageType.COLLECTION_REMOVE, {
      videoViewId: videoViewId ?? 0, // 0 means search all caches
      collectionId,
    })
  },

  /**
   * Check if a word is collected.
   *
   * @param videoViewId - Video view ID
   * @param word - Word to check
   * @param segmentBegin - Segment start time (ms)
   * @returns true if collected
   */
  async isWordCollected(videoViewId: number, word: string, segmentBegin: number): Promise<boolean> {
    const response = await sendMessageWithResponse<{ collected: boolean }>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.COLLECTION_CHECK,
      { videoViewId, word, segmentBegin },
    )
    return response.collected
  },

  /**
   * Get collection ID for a word (used for unfavorite).
   *
   * @param videoViewId - Video view ID
   * @param word - Word to look up
   * @param segmentBegin - Segment start time (ms)
   * @returns Collection ID or null
   */
  async getCollectionId(
    videoViewId: number,
    word: string,
    segmentBegin: number,
  ): Promise<string | null> {
    const response = await sendMessageWithResponse<{ collectionId: string | null }>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.COLLECTION_CHECK,
      { videoViewId, word, segmentBegin, returnId: true },
    )
    return response.collectionId
  },

  /**
   * Get collected lemmas for sidebar highlighting.
   *
   * @param videoViewId - Video view ID
   * @returns Set of lowercase lemmas
   */
  async getCollectedLemmas(videoViewId: number): Promise<Set<string>> {
    const response = await sendMessageWithResponse<{ lemmas: string[] }>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.COLLECTION_GET_LEMMAS,
      { videoViewId },
    )
    return new Set(response.lemmas)
  },

  /**
   * Create a new collection.
   * Calls API via Background, updates cache, and broadcasts to all tabs.
   *
   * @param request - Video collection create request
   * @returns Created collection response
   */
  async createCollection(request: VideoCollectionCreateRequest): Promise<VideoCollectionResponse> {
    const response = await sendMessageWithResponse<{
      success: boolean
      collection?: VideoCollectionResponse
      error?: string
    }>(MessageSource.CONTENT_SCRIPT, MessageType.COLLECTION_CREATE, { request })

    if (!response.success || !response.collection) {
      throw new Error(response.error || 'Failed to create collection')
    }
    return response.collection
  },
}
