import { SubtitleSegment, Phrase } from '../subtitle'
import { EntryLookupResponse } from '../../tools/client'

/**
 * CJK (Chinese, Japanese, Korean) 语言代码列表
 * 这些语言需要特殊的音标/罗马音处理
 */
export const CJK_LANGUAGES = ['zh-CN', 'zh-TW', 'ja', 'ko'] as const

/**
 * 中文语言代码列表（用于只显示 romanization/拼音的场景）
 */
export const CHINESE_LANGUAGES = ['zh-CN', 'zh-TW'] as const

/**
 * 检查语言是否为 CJK 语言
 */
export function isCJKLanguage(langCode: string | undefined): boolean {
  return CJK_LANGUAGES.includes(langCode as (typeof CJK_LANGUAGES)[number])
}

/**
 * 检查语言是否为中文
 */
export function isChineseLanguage(langCode: string | undefined): boolean {
  return CHINESE_LANGUAGES.includes(langCode as (typeof CHINESE_LANGUAGES)[number])
}

/**
 * Tooltip 状态定义
 */
export type TooltipState =
  | 'hidden'
  | 'loading'
  | 'shortHover'
  | 'shortLocked'
  | 'expanded'
  | 'translation'

/**
 * Tooltip 事件定义 (discriminated union)
 */
export type TooltipEvent =
  | { type: 'HOVER_WORD'; word: string; segment: SubtitleSegment; segmentIndex: string }
  | { type: 'HOVER_NON_WORD'; segment: SubtitleSegment; segmentIndex: string }
  | { type: 'CLICK_WORD'; word: string }
  | { type: 'MOUSE_OUT' }
  | { type: 'LOAD_SUCCESS'; definitions: string }
  | { type: 'LOAD_ERROR' }
  | { type: 'CLOSE_TOOLTIP' }

/**
 * Data required for rendering the short (hover) tooltip.
 */
export interface ShortContentData {
  word: string
  definitions: string
  phrases: Phrase[]
  translation?: string
  isError?: boolean
  phonetic?: string // 音标（日语/韩语为假名/韩文读音）
  romanization?: string // 罗马音（日语为 romaji，韩语为罗马字）
  lang_code?: string // 语言代码（用于音标格式化）
  tokenPos?: string // Token 原始词性（来自 NLP 分析）
  targetLang?: string // 目标语言（被学习的语言，用于 POS 本地化）
}

/**
 * Data required for rendering the expanded tooltip.
 * 所有词条数据直接从 entryResponse 提取，避免中间数据结构
 */
export interface ExpandedContentData {
  word: string
  cachedDefinition: string
  phrases: Phrase[]
  translation?: string
  lang_code?: string
  showLoadingSpinner?: boolean
  tokenPos?: string // Token 原始词性（来自 NLP 分析，固定不变）
  lemma?: string // 词元（原形）
  selectedPos?: string // 当前选中的词性（用于 POS switcher）
  isFavorited?: boolean // 是否已收藏
  entryResponse?: EntryLookupResponse // 完整的 API 响应（senses, examples 等从这里提取）
  targetLang?: string // 目标语言（被学习的语言，用于 POS 本地化）
  phonetic?: string // 音标（由 controller 从 entry 或 linguisticToken 提取）
  romanization?: string // 罗马音（由 controller 从 entry 或 linguisticToken 提取）
}
