/**
 * Message utilities for Chrome extension communication
 *
 * Provides two patterns for sending messages to Background Service:
 * 1. sendMessageWithResponse - Request-Response pattern with timeout
 * 2. sendMessageFireAndForget - Fire-and-Forget pattern (no response expected)
 */

import { MessageSource, MessageType } from '../types/messages'

const DEFAULT_TIMEOUT = 10000 // 10 seconds

/**
 * Send a message to Background Service and wait for response with timeout.
 *
 * Use this for operations that require a response:
 * - Authentication checks (CHECK_AUTH)
 * - Data retrieval (GET_SUBTITLE_DATA)
 * - Login/Logout operations
 *
 * @param source - Message source identifier
 * @param type - Message type
 * @param data - Optional data payload
 * @param timeout - Timeout in milliseconds (default: 10000ms)
 * @returns Promise resolving to the response
 * @throws Error if timeout or chrome.runtime.lastError
 */
export function sendMessageWithResponse<T>(
  source: MessageSource,
  type: MessageType,
  data: Record<string, unknown> = {},
  timeout: number = DEFAULT_TIMEOUT,
): Promise<T> {
  return new Promise((resolve, reject) => {
    const timer = setTimeout(() => {
      reject(new Error(`Message ${type} timed out after ${timeout}ms`))
    }, timeout)

    chrome.runtime.sendMessage(
      {
        source,
        type,
        data,
        timestamp: Date.now(),
      },
      (response) => {
        clearTimeout(timer)

        if (chrome.runtime.lastError) {
          reject(new Error(chrome.runtime.lastError.message || 'Unknown error'))
        } else {
          resolve(response)
        }
      },
    )
  })
}

/**
 * Send a message to Background Service without waiting for response.
 *
 * Use this for notifications that don't require acknowledgment:
 * - INIT_BACKGROUND
 * - VIDEO_CURRENT_TIMESTAMP
 * - FINISH_TRANSLATE
 * - OPEN_POPUP
 *
 * @param source - Message source identifier
 * @param type - Message type
 * @param data - Optional data payload
 */
export function sendMessageFireAndForget(
  source: MessageSource,
  type: MessageType,
  data: Record<string, unknown> = {},
): void {
  chrome.runtime.sendMessage({
    source,
    type,
    data,
    timestamp: Date.now(),
  })
}
