/**
 * Client Bridge - Proxy API calls through Background Service
 *
 * This module provides a way to call APIClient methods from Popup and
 * Content Script contexts through the Background Service, ensuring
 * single source of truth for token management.
 *
 * Usage:
 *   import { callAPI } from '../tools/clientBridge'
 *   const collections = await callAPI('getVideoCollections', { skip: 0, limit: 20 })
 */

import { sendMessageWithResponse } from './messageUtils'
import { MessageSource, MessageType } from '../types/messages'

// Define the methods that can be called through the bridge
// These are the public async methods of APIClient
export type BridgeableMethods =
  // Video Collections
  | 'getVideoCollections'
  | 'getVideoCollection'
  | 'deleteVideoCollection'
  | 'createVideoCollection'
  // Video Views
  | 'listVideoViews'
  | 'getVideoView'
  | 'addVideoView'
  // Dictionary
  | 'lookupWord'
  | 'lookupEntry'
  // NLP
  | 'detectLanguage'
  | 'analyzeText'

interface APICallResponse<T> {
  success: boolean
  data?: T
  error?: string
}

/**
 * Call an APIClient method through the Background Service.
 * Works from both Popup and Content Script contexts.
 *
 * @param method - The APIClient method name to call
 * @param args - Arguments to pass to the method (spread as individual args)
 * @returns The result from the API call
 * @throws Error if the API call fails
 *
 * @example
 * // Single object argument
 * const collections = await callAPI('getVideoCollections', { skip: 0, limit: 20 })
 *
 * // Multiple arguments
 * const entry = await callAPI('lookupEntry', 'run', 'en', 'zh-CN')
 *
 * // No arguments
 * const user = await callAPI('getCurrentUser')
 */
export async function callAPI<T>(method: BridgeableMethods, ...args: unknown[]): Promise<T> {
  // Determine the source based on context
  // Content scripts and popup both use chrome.runtime.sendMessage
  // We use CONTENT_SCRIPT as a generic source for non-background contexts
  const source = MessageSource.CONTENT_SCRIPT

  const response = await sendMessageWithResponse<APICallResponse<T>>(source, MessageType.API_CALL, {
    method,
    args,
  })

  if (!response?.success) {
    throw new Error(response?.error || `API call '${method}' failed`)
  }

  return response.data as T
}
