/**
 * i18n utility module for Chrome Extension
 * Supports custom interface language selection
 */

import { SUPPORTED_LOCALES, isSupportedLocale } from '../context'

type Messages = Record<string, { message: string; description?: string }>
type MessagesCache = Record<string, Messages>

const DEFAULT_LOCALE = 'en'
const STORAGE_KEY = 'interfaceLanguage'

// Cache for all locale messages
let messagesCache: MessagesCache = {}
let currentLocale: string = DEFAULT_LOCALE
let browserLocale: string = DEFAULT_LOCALE // Browser's UI language (for unauthenticated state)

/**
 * Initialize i18n system by loading all locale files
 */
export async function initI18n(): Promise<void> {
  // Determine browser's UI language (for unauthenticated state)
  const browserLang = chrome.i18n.getUILanguage()
  const normalizedBrowserLang = normalizeLocale(browserLang)
  browserLocale = isSupportedLocale(normalizedBrowserLang) ? normalizedBrowserLang : DEFAULT_LOCALE

  // Get user's preferred interface language from storage
  const stored = await chrome.storage.sync.get([STORAGE_KEY])
  const userLocale = stored[STORAGE_KEY]

  // Determine which locale to use for authenticated state
  if (userLocale && isSupportedLocale(userLocale)) {
    currentLocale = userLocale
  } else {
    // Fallback to browser language
    currentLocale = browserLocale
  }

  // Load messages for both browser locale and current locale
  await Promise.all([loadMessages(browserLocale), loadMessages(currentLocale)])
}

/**
 * Load messages for a specific locale
 */
async function loadMessages(locale: string): Promise<void> {
  if (messagesCache[locale]) {
    return // Already loaded
  }

  try {
    const url = chrome.runtime.getURL(`_locales/${locale}/messages.json`)
    const response = await fetch(url)
    const messages = await response.json()
    messagesCache[locale] = messages
  } catch (error) {
    console.error(`[i18n] Failed to load messages for locale: ${locale}`, error)
    // Fallback to default locale
    if (locale !== DEFAULT_LOCALE) {
      await loadMessages(DEFAULT_LOCALE)
    }
  }
}

/**
 * Normalize locale code (e.g., "zh-CN" -> "zh_CN")
 */
function normalizeLocale(locale: string): string {
  return locale.replace('-', '_')
}

/**
 * Get internationalized text (uses user's interface language setting)
 * @param key - i18n message key (e.g., "popup_title")
 * @param substitutions - Optional placeholder substitutions
 * @returns Translated message text
 */
export function t(key: string, substitutions?: string | string[]): string {
  const messages = messagesCache[currentLocale] || messagesCache[DEFAULT_LOCALE]

  if (!messages) {
    console.warn(
      `[i18n] No messages loaded for locale: ${currentLocale}, cache keys:`,
      Object.keys(messagesCache),
    )
    return key
  }

  if (!messages[key]) {
    console.warn(
      `[i18n] Missing translation key: ${key} for locale: ${currentLocale}`,
      `Available keys: ${Object.keys(messages).length}`,
    )
    return key // Return key itself for debugging
  }

  let message = messages[key].message

  // Handle substitutions
  if (substitutions) {
    const subs = Array.isArray(substitutions) ? substitutions : [substitutions]
    subs.forEach((sub, index) => {
      message = message.replace(`$${index + 1}`, sub)
    })
  }

  return message
}

/**
 * Get internationalized text using browser's UI language (for unauthenticated state)
 * Use this in login/register screens and unauthenticated components
 * @param key - i18n message key (e.g., "auth_login_button")
 * @param substitutions - Optional placeholder substitutions
 * @returns Translated message text in browser's language
 */
export function tBrowser(key: string, substitutions?: string | string[]): string {
  const messages = messagesCache[browserLocale] || messagesCache[DEFAULT_LOCALE]

  if (!messages) {
    console.warn(
      `[i18n] No messages loaded for browser locale: ${browserLocale}, cache keys:`,
      Object.keys(messagesCache),
    )
    return key
  }

  if (!messages[key]) {
    console.warn(
      `[i18n] Missing translation key: ${key} for browser locale: ${browserLocale}`,
      `Available keys: ${Object.keys(messages).length}`,
    )
    return key
  }

  let message = messages[key].message

  // Handle substitutions
  if (substitutions) {
    const subs = Array.isArray(substitutions) ? substitutions : [substitutions]
    subs.forEach((sub, index) => {
      message = message.replace(`$${index + 1}`, sub)
    })
  }

  return message
}

/**
 * Get current interface language
 * @returns Language code (e.g., "zh_CN", "en", "ja")
 */
export function getCurrentLocale(): string {
  return currentLocale
}

/**
 * Set interface language and reload messages
 * @param locale - Language code (e.g., "zh_CN", "en", "ja")
 */
export async function setInterfaceLanguage(locale: string): Promise<void> {
  if (!isSupportedLocale(locale)) {
    console.error(`[i18n] Unsupported locale: ${locale}`)
    return
  }

  currentLocale = locale
  await chrome.storage.sync.set({ [STORAGE_KEY]: locale })
  await loadMessages(locale)
}

/**
 * Get list of supported locales
 */
export function getSupportedLocales(): string[] {
  return [...SUPPORTED_LOCALES]
}

/**
 * Get current browser UI language
 * @returns Language code (e.g., "zh-CN", "en", "ja")
 */
export function getBrowserLanguage(): string {
  return chrome.i18n.getUILanguage()
}
