/**
 * Global context and configuration for Moki extension.
 *
 * Defines supported languages and provides utility functions
 * for language name lookups using i18n.
 */

import { t } from './i18n'

// ============================================================================
// Supported Languages
// ============================================================================

/**
 * Supported language codes for native language and target language.
 * These codes are used for API calls (e.g., translation, NLP).
 *
 * Format: BCP 47 language tags (e.g., 'zh-CN', 'en', 'ja')
 */
export const SUPPORTED_LANGUAGES = ['en', 'zh-CN', 'zh-TW', 'ja', 'ko', 'es', 'fr', 'de'] as const

export type SupportedLanguageCode = (typeof SUPPORTED_LANGUAGES)[number]

/**
 * Supported locale codes for interface language.
 * These codes are used for i18n (Chrome extension _locales format).
 *
 * Format: underscore-separated (e.g., 'zh_CN', 'en', 'ja')
 */
export const SUPPORTED_LOCALES = ['en', 'zh_CN', 'zh_TW', 'ja', 'ko', 'es', 'fr', 'de'] as const

export type SupportedLocaleCode = (typeof SUPPORTED_LOCALES)[number]

/**
 * Check if a locale code is supported.
 * Type-safe version of SUPPORTED_LOCALES.includes()
 */
export function isSupportedLocale(locale: string): locale is SupportedLocaleCode {
  return (SUPPORTED_LOCALES as readonly string[]).includes(locale)
}

// ============================================================================
// Language Name Utilities
// ============================================================================

/**
 * Convert language code to i18n message key.
 * e.g., 'zh-CN' -> 'languages_zhCN', 'en' -> 'languages_en'
 */
function languageCodeToI18nKey(code: string): string {
  // Remove hyphen and create key: 'zh-CN' -> 'zhCN', 'en' -> 'en'
  const normalizedCode = code.replace('-', '')
  return `languages_${normalizedCode}`
}

/**
 * Get localized language name from language code.
 * Uses i18n to get the name in the user's interface language.
 *
 * @param code - Language code (e.g., 'zh-CN', 'en', 'ja')
 * @returns Localized language name (e.g., '简体中文', 'English', '日本語')
 */
export function getLanguageName(code: string): string {
  const key = languageCodeToI18nKey(code)
  const name = t(key)
  // If translation not found, return the code itself
  return name !== key ? name : code
}

/**
 * Convert locale code to language code.
 * e.g., 'zh_CN' -> 'zh-CN', 'en' -> 'en'
 */
export function localeToLanguageCode(locale: string): string {
  return locale.replace('_', '-')
}

/**
 * Convert language code to locale code.
 * e.g., 'zh-CN' -> 'zh_CN', 'en' -> 'en'
 */
export function languageCodeToLocale(code: string): string {
  return code.replace('-', '_')
}
